.stars {
  display: flex;

  &--4 :nth-child(-n + 4) {
    background-image: url(/src/images/star-active.svg);
  }

  &__star {
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    height: 16px;
    width: 16px;

    &:not(:last-child) {
      margin-right: 4px;
    }
  }
}
