.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.catalog {
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(1, 1fr);
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 1fr);
  }
}

.nav {
  background-color: #fff;
  display: flex;
}

.nav__item {
  margin: 0 10px;
  text-decoration: none;
  list-style: none;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__list :first-child {
  margin-left: 0;
}

.nav__list :last-child {
  margin-right: 0;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
  transition: all .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__logo {
  background-image: url("imac.202c52ff.jpeg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 160px;
  height: 134px;
  margin: 32px 19px 40px;
}

.card__content {
  margin: 0 16px 16px;
}

.card__header {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__evaluation {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__buy-button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

body {
  margin: 0;
  padding: 0;
}

.logo {
  width: 40px;
  height: 40px;
}
